@use "../../styles/colors" as c;

.calc {
	background-color: c.$white;
	padding: 4.5rem 4rem;
	border-radius: 3rem;
	border-bottom-right-radius: 13rem;
	width: 60vw;
}

.controlGroup {
	display: flex;
	column-gap: 3rem;
}

.test {
	display: flex;
	flex-direction: row;
	align-items: center;

	hr {
		width: 100%;
		height: 1px;
		border: none;
		background-color: c.$light-grey;
	}

	button {
		background-color: c.$purple;
		border: none;
		padding: 20px;
		border-radius: 50%;
		cursor: pointer;

		img {
			aspect-ratio: 1/1;
		}

		&:hover {
			background-color: c.$off-black;
		}
	}
}
