@use "../../styles/colors" as c;

div:has(> span:not(:empty)) input {
	border-color: c.$light-red;
}

label,
::placeholder {
	color: c.$smokey-grey;
}

label {
	font-size: 1.5rem;
	letter-spacing: 0.3rem;
}

input {
	// color: #000;
	font-size: 3.5rem;
	width: 140px;
	padding: 0.2em 0.5em;
	border: 1px solid c.$light-grey;
	border-radius: 10px;

	&:hover,
	&:focus,
	&:focus-visible {
		outline: none !important;
		border: 1px solid c.$purple;
	}
}

.err {
	color: c.$light-red;
	font-weight: 400;
}
